export class OrderService {
    constructor(events) {
        this.events = events;
        this.payment = '';
        this.email = '';
        this.phone = '';
        this.address = '';
        this.total = 0;
        this.items = [];
        this.errors = {};
    }
    setAddress(field, value) {
        if (field === 'address') {
            this.address = value;
        }
        if (this.validateOrder()) {
            this.events.emit('order:completed', this.updateOrderData());
        }
    }
    validateOrder() {
        const errors = {};
        if (!this.address) {
            errors.address = 'Address is required';
        }
        else if (!this.isValidAddress(this.address)) {
            errors.address = 'Please provide a valid address';
        }
        if (!this.payment) {
            errors.payment = 'Payment method is required';
        }
        else if (!this.isValidPayment(this.payment)) {
            errors.payment = 'Invalid payment method';
        }
        this.errors = errors;
        this.events.emit('orderError:address&paymentMethod', this.errors);
        return this.isFormValid(errors);
    }
    isValidPayment(payment) {
        const validPayments = ['cash', 'card'];
        return validPayments.includes(payment);
    }
    isValidAddress(address) {
        const addressRegex = /^[а-яА-ЯёЁa-zA-Z0-9\s/.,-]{7,}$/;
        return addressRegex.test(address);
    }
    isFormValid(errors) {
        return Object.keys(errors).length === 0;
    }
    setContactData(field, value) {
        if (field === 'email') {
            this.email = value;
        }
        else if (field === 'phone') {
            this.phone = value;
        }
        if (this.validateContacts()) {
            this.events.emit('order:completed', this.updateOrderData());
        }
    }
    validateContacts() {
        const errors = {};
        this.validateEmail(errors);
        this.validatePhone(errors);
        this.errors = errors;
        this.events.emit('orderError:isValid', this.errors);
        return this.isFormValid(errors);
    }
    validateEmail(errors) {
        const emailRegex = /^(?![.-])[A-Za-z0-9._%+-]+(?:[A-Za-z0-9.-]*[A-Za-z0-9])?@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/;
        if (!this.email) {
            errors.email = 'Email is required';
        }
        else if (!emailRegex.test(this.email)) {
            errors.email = 'Invalid email address';
        }
    }
    validatePhone(errors) {
        const phoneRegex = /^(?:\+7|8)[\s-]?\(?\d{3}\)?[\s-]?\d{3}[\s-]?\d{2}[\s-]?\d{2}$/;
        if (this.phone && this.phone[0] === '8') {
            this.phone = `+7${this.phone.slice(1)}`;
        }
        if (!this.phone) {
            errors.phone = 'Phone number is required';
        }
        else if (!phoneRegex.test(this.phone)) {
            errors.phone = 'Invalid phone number format';
        }
    }
    updateOrderData() {
        return {
            payment: this.payment,
            email: this.email,
            phone: this.phone,
            address: this.address,
            total: this.total,
            items: this.items,
        };
    }
}
